<!--
 * @description 聊天列表页面
 -->
<template>
	<view class="chat-list-container">
		<!-- 未登录状态 -->
		<view class="login-tip" v-if="!userInfo">
			<text>请先登录</text>
			<button class="login-btn" @click="goToLogin">去登录</button>
		</view>
		
		<!-- 已登录状态 -->
		<block v-else>
			<view class="chat-list">
				<view 
					class="chat-item"
					v-for="chat in chatList"
					:key="chat.user._id"
					@click="goToChat(chat.user._id)"
				>
					<!-- 头像 -->
					<view class="avatar-section">
						<image 
							class="avatar"
							:src="chat.user.avatar"
							mode="aspectFill"
						></image>
						<view class="unread-badge" v-if="chat.unreadCount > 0">
							{{ chat.unreadCount > 99 ? '99+' : chat.unreadCount }}
						</view>
					</view>
					
					<!-- 聊天信息 -->
					<view class="chat-info">
						<view class="chat-header">
							<text class="user-name">{{ chat.user.name }}</text>
							<text class="chat-time">{{ formatTime(chat.lastMessage.createTime) }}</text>
						</view>
						
						<view class="last-message">
							<text v-if="chat.lastMessage.type === 1">{{ chat.lastMessage.content }}</text>
							<text v-else-if="chat.lastMessage.type === 2">[图片]</text>
						</view>
					</view>
				</view>
			</view>
			
			<!-- 空状态 -->
			<view class="empty-state" v-if="chatList.length === 0">
				<text>暂无聊天记录</text>
			</view>
		</block>
	</view>
</template>

<script>
export default {
	data() {
		return {
			userInfo: null,
			chatList: []
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo')
		if (this.userInfo) {
			this.loadChatList()
		}
	},
	methods: {
		async loadChatList() {
			uni.showLoading({
				title: '加载中'
			})
			
			try {
				const result = await uniCloud.callFunction({
					name: 'messageCenter',
					data: {
						action: 'getChatList',
						data: {
							userId: this.userInfo.userId
						}
					}
				})
				
				if (result.result.code === 0) {
					this.chatList = result.result.data
				}
			} catch (e) {
				uni.showToast({
					title: '加载失败',
					icon: 'none'
				})
			} finally {
				uni.hideLoading()
			}
		},
		
		goToChat(userId) {
			uni.navigateTo({
				url: `/pages/chat/chat?id=${userId}`
			})
		},
		
		goToLogin() {
			uni.navigateTo({
				url: '/pages/login/login'
			})
		},
		
		formatTime(timestamp) {
			const date = new Date(timestamp)
			const now = new Date()
			
			// 如果是今天的消息，只显示时间
			if (date.toDateString() === now.toDateString()) {
				return `${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`
			}
			
			// 如果是昨天的消息，显示"昨天"
			const yesterday = new Date(now)
			yesterday.setDate(now.getDate() - 1)
			if (date.toDateString() === yesterday.toDateString()) {
				return `昨天 ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`
			}
			
			// 其他日期显示完整日期
			return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`
		}
	}
}
</script>

<style>
.chat-list-container {
	min-height: 100vh;
	background-color: #f8f8f8;
}

.login-tip {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 100rpx 0;
}

.login-tip text {
	font-size: 32rpx;
	color: #666;
	margin-bottom: 40rpx;
}

.login-btn {
	width: 240rpx;
	height: 80rpx;
	background-color: #007AFF;
	color: #fff;
	border-radius: 40rpx;
	font-size: 32rpx;
	display: flex;
	align-items: center;
	justify-content: center;
}

.chat-list {
	background-color: #fff;
}

.chat-item {
	display: flex;
	align-items: center;
	padding: 30rpx;
	border-bottom: 2rpx solid #f5f5f5;
}

.avatar-section {
	position: relative;
	margin-right: 30rpx;
}

.avatar {
	width: 100rpx;
	height: 100rpx;
	border-radius: 50rpx;
}

.unread-badge {
	position: absolute;
	top: -10rpx;
	right: -10rpx;
	min-width: 40rpx;
	height: 40rpx;
	background-color: #ff4757;
	color: #fff;
	border-radius: 20rpx;
	font-size: 24rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 10rpx;
}

.chat-info {
	flex: 1;
}

.chat-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10rpx;
}

.user-name {
	font-size: 32rpx;
	color: #333;
	font-weight: bold;
}

.chat-time {
	font-size: 24rpx;
	color: #999;
}

.last-message {
	font-size: 28rpx;
	color: #666;
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.empty-state {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 400rpx;
}

.empty-state text {
	font-size: 28rpx;
	color: #999;
}
</style> 